body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ol, ul {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
}

.rule_no {
  font-weight: bold;
}

.note {
  margin-left: 1em;
  font-size: small;
}

.articleLine {
  margin-bottom: 0.5em;
}

.deprecation {
  border: 1px solid red;
  padding: 0.5em;
  margin: 0.5em;
}

.App {
  margin: 0 auto;
}

.App > * {
  padding: 0 .5em;
}

.App > header {
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  min-height: 3rem;
  color: white;
}

.App > main {
  min-height: calc(100vh - 5rem);
}

.App > footer {
  background-color: gray;
  min-height: 2rem;
  text-align: right;
  color: #fff;
}

.App-link {
  font-weight: bold;
  color: inherit;
  text-decoration: none;
}

.App-footer-link {
  color: inherit;
  text-decoration: none;
}

